<!-- 未中奖弹窗 -->
<template>
	<xh-dialog ref="xhDialog" animName="fadeInDownBig" @closeNotice="$emit('closeNotice')">
		 <view class="no-win-prize">
			 <!-- 未中奖提示 -->
			 <view class="msg">{{msg}}</view>
			 <!-- 副消息 -->
			 <view class="msg-small" v-if="msgSmall">
			 	{{msgSmall}}
			 </view>
			 <!-- 马上兑奖 -->
			 <view class="xh-toast-button go-niuniu" @click="goNiuNiu">进入牛牛乐园</view>
		 </view>
    </xh-dialog>
</template>

<script>
	export default{
		data(){
			return {
				msg:'',
				msgSmall:''
			}
		},
		methods:{
			show(msg){
				this.msg = msg;
				if(msg == '无效二维码')this.msgSmall = "温馨提示：请扫红牛维生素功能饮料（250ML）的拉环二维码";
				this.$refs.xhDialog.show();
			},
			goNiuNiu(){
				uni.navigateToMiniProgram({
				  appId: 'wx6aef93f01a5d8d05',//小程序appid
				  envVersion: 'develop',
				  success(res) {
				    console.log(res,'跳转成功')
				  }
				})
				this.$refs.xhDialog.close();
			}
		}
	}
</script>

<style lang="scss">
	 .no-win-prize{
		 position: relative;
		 z-index: 1;
		 padding: 0 RPX(38);
		 .msg{
			 padding-top: RPX(38);
			 font-size: RPX(25);
			 color: #fff;
			 text-align: center;
			 font-weight: bold;
		 }
		 .go-niuniu{
			 margin-top: RPX(80);
			 margin-bottom: RPX(20);
		 }
		 .msg-small{
			 font-size: RPX(13);
			 color: #F6E2B3;
			 padding-top: RPX(15);
			 padding: RPX(15) RPX(40) 0 RPX(40);
			 text-align: center;
		 }
	 }
</style>
